import { LoadingButton } from '@mui/lab';
import { Button, Grid, Link, Stack, Typography } from '@mui/material';
import { enqueueSnackbar } from '@riptech/minimal-ui/src/components/snackbar';
import type { FC } from 'react';
import React, { useCallback, useState } from 'react';
import { useAppContext } from '../hooks/useAppContext';
interface EmailVerificationPageProps {
  readonly logout: () => Promise<void>;
  readonly resendVerificationEmail: () => Promise<void>;
}
const EmailVerificationPage: FC<EmailVerificationPageProps> = ({
  resendVerificationEmail,
  logout
}) => {
  const {
    appEmail,
    appName
  } = useAppContext();
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const sendVerificationEmail = useCallback(async () => {
    setIsLoading(true);
    try {
      // Resend verification email
      await resendVerificationEmail();
      enqueueSnackbar('A verification email was sent to your email address. Please check your inbox.', {
        variant: 'success'
      });
    } catch (err) {
      enqueueSnackbar('An error occurred while resending verification email', {
        variant: 'error'
      });
    } finally {
      setIsLoading(false);
    }
  }, [resendVerificationEmail]);
  return <Grid container justifyContent="center" spacing={4} data-sentry-element="Grid" data-sentry-component="EmailVerificationPage" data-sentry-source-file="email-verification.tsx">
      <Grid item xs={12} data-sentry-element="Grid" data-sentry-source-file="email-verification.tsx">
        <Typography variant="h4" data-sentry-element="Typography" data-sentry-source-file="email-verification.tsx">Email Verification Required</Typography>
      </Grid>

      <Grid item xs={12} data-sentry-element="Grid" data-sentry-source-file="email-verification.tsx">
        <Typography color="text.secondary" variant="body1" data-sentry-element="Typography" data-sentry-source-file="email-verification.tsx">
          Before you can use {appName}, you must verify your email. If you do not see the email, please check your spam
          folder or request a new email verification request by clicking the button below.
        </Typography>
      </Grid>

      <Grid item xs={12} data-sentry-element="Grid" data-sentry-source-file="email-verification.tsx">
        <Typography color="text.secondary" variant="body1" data-sentry-element="Typography" data-sentry-source-file="email-verification.tsx">
          For further assistance, please contact <Link href={`mailto:${appEmail}`} data-sentry-element="Link" data-sentry-source-file="email-verification.tsx">support</Link>.
        </Typography>
      </Grid>

      <Grid item xs={12} data-sentry-element="Grid" data-sentry-source-file="email-verification.tsx">
        <Stack alignItems="center" direction={{
        sm: 'row',
        xs: 'column'
      }} spacing={1} data-sentry-element="Stack" data-sentry-source-file="email-verification.tsx">
          <Button color="inherit" fullWidth onClick={logout} variant="outlined" data-sentry-element="Button" data-sentry-source-file="email-verification.tsx">
            Logout
          </Button>
          <LoadingButton color="inherit" fullWidth loading={isLoading} onClick={sendVerificationEmail} variant="contained" data-sentry-element="LoadingButton" data-sentry-source-file="email-verification.tsx">
            Resend Verification
          </LoadingButton>
        </Stack>
      </Grid>
    </Grid>;
};
export default EmailVerificationPage;