import {
  getGridDateOperators,
  getGridNumericOperators,
  getGridStringOperators,
  type GridFilterItem,
  type GridFilterModel,
  GridLogicOperator,
  type GridSortModel
} from '@mui/x-data-grid-pro';
import type { SQL, SQLWrapper } from 'drizzle-orm';
import { and, asc, desc, eq, gt, gte, ilike, inArray, lt, lte, not, notIlike, or, sql } from 'drizzle-orm';
import type { PgTableWithColumns, TableConfig } from 'drizzle-orm/pg-core';
import moment from 'moment-timezone';

export default class DataGridUtils {
  static createDrizzleFilter<T extends TableConfig>(
    table: PgTableWithColumns<T>,
    { items, logicOperator = GridLogicOperator.And }: GridFilterModel
  ): SQL | undefined {
    const drizzleFilters: Array<SQLWrapper> = [];

    items
      .filter(({ value }) => value !== undefined)
      .forEach((filter) => {
        const { field, operator, value } = filter;
        if (!table[field]) {
          return;
        }

        switch (operator) {
          // String filters
          case 'contains':
            drizzleFilters.push(ilike(table[field], `%${value}%`));
            break;
          case 'doesNotContain':
            drizzleFilters.push(notIlike(table[field], `%${value}%`));
            break;
          case 'startsWith':
            drizzleFilters.push(ilike(table[field], `${value}%`));
            break;
          case 'endsWith':
            drizzleFilters.push(ilike(table[field], `%${value}`));
            break;
          case 'equals':
            drizzleFilters.push(ilike(table[field], value));
            break;
          // Select filters
          case 'is':
            drizzleFilters.push(eq(table[field], value));
            break;
          case 'not':
            drizzleFilters.push(not(eq(table[field], value)));
            break;
          case 'isAnyOf':
            if (value.length) {
              drizzleFilters.push(inArray(table[field], value));
            }
            break;
          // Date filters
          case 'after':
            drizzleFilters.push(gt(table[field], moment(value).endOf('day').toISOString()));
            break;
          case 'onOrAfter':
            drizzleFilters.push(gte(table[field], moment(value).endOf('day').toISOString()));
            break;
          case 'before':
            drizzleFilters.push(lt(table[field], moment(value).startOf('day').toISOString()));
            break;
          case 'onOrBefore':
            drizzleFilters.push(lte(table[field], moment(value).startOf('day').toISOString()));
            break;
          // Number filters
          case '=':
            drizzleFilters.push(eq(table[field], Number.isFinite(+value) ? +value : undefined));
            break;
          case '!=':
            drizzleFilters.push(not(eq(table[field], Number.isFinite(+value) ? +value : undefined)));
            break;
          case '>':
            drizzleFilters.push(gt(table[field], Number.isFinite(+value) ? +value : undefined));
            break;
          case '>=':
            drizzleFilters.push(gte(table[field], Number.isFinite(+value) ? +value : undefined));
            break;
          case '<':
            drizzleFilters.push(lt(table[field], Number.isFinite(+value) ? +value : undefined));
            break;
          case '<=':
            drizzleFilters.push(lte(table[field], Number.isFinite(+value) ? +value : undefined));
            break;
          // Add more cases for other operators if needed
          default:
            // Unknown operator
            break;
        }
      });

    if (!drizzleFilters.length) {
      return undefined;
    }

    return logicOperator === GridLogicOperator.And ? and(...drizzleFilters) : or(...drizzleFilters);
  }

  static createDrizzleSort<T extends TableConfig>(table: PgTableWithColumns<T>, sortModel: GridSortModel) {
    return sortModel
      .filter(({ sort }) => sort)
      .map(({ field, sort }) => {
        const sortValue = table[field].columnType === 'string' ? sql`lower(${table[field]})` : table[field];
        if (sort === 'asc') {
          return asc(sortValue);
        }

        return desc(sortValue);
      });
  }

  static createCustomSingleSelectFilter<T extends string = string>(
    filters: Array<GridFilterItem>,
    logicOperator: GridLogicOperator,
    options: Array<T>,
    getSingleFilterValue: (item: T) => SQLWrapper
  ) {
    if (!filters.length) {
      return undefined;
    }

    const operationResults: Array<SQLWrapper | undefined> = [];
    filters.forEach((filter) => {
      if (filter.value) {
        switch (filter.operator) {
          case 'is':
            operationResults.push(getSingleFilterValue(filter.value));
            break;
          case 'not':
            operationResults.push(
              or(...options.filter((status) => status !== filter.value).map((value) => getSingleFilterValue(value)))
            );
            break;
          case 'isAnyOf':
            operationResults.push(or(...(filter.value || []).map((status: T) => getSingleFilterValue(status))));
            break;
          default:
        }
      }
    });

    return logicOperator === GridLogicOperator.And ? and(...operationResults) : or(...operationResults);
  }

  static STRING_FILTERS = getGridStringOperators().filter((operator) =>
    ['contains', 'doesNotContain', 'startsWith', 'endsWith', 'equals'].includes(operator.value)
  );

  static NUMBER_FILTERS = getGridNumericOperators().filter((operator) =>
    ['=', '!=', '>', '>=', '<', '<='].includes(operator.value)
  );

  static DATE_FILTERS = getGridDateOperators().filter((operator) =>
    ['after', 'onOrAfter', 'before', 'onOrBefore'].includes(operator.value)
  );
}
