import type { Action, Dispatch } from '@reduxjs/toolkit';
import { enqueueSnackbar } from '@riptech/minimal-ui/src/components/snackbar';

export default class NotificationUtils {
  static dismissNotification = async <T extends { id: string }>(
    reduxNotifications: Array<T>,
    notificationId: string,
    dispatch: Dispatch,
    setNotifications: (newNotifications: Array<T>) => Action,
    dismissNotification: (notification: T) => Promise<Record<string, never>>,
    closeNotificationDrawer: () => void
  ) => {
    try {
      await dismissNotification({ id: notificationId } as T);

      const newNotifications = reduxNotifications.filter((n) => n.id !== notificationId);
      dispatch(setNotifications(newNotifications));

      if (!newNotifications.length) {
        closeNotificationDrawer();
      }
    } catch (err) {
      // Show an error snackbar
      enqueueSnackbar('An error occurred while dismissing notification', { variant: 'error' });
    }
  };
}
